
.header{
  color: #696763;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  font-weight: 300;
  margin-bottom: 30px;
  margin-top: 50px;
}
.form-input {
   background: #F0F0E9;
   border: medium none;
   color: #9d9d9d;
   font-family: 'roboto';
   font-size: 14px;
   font-weight: 300;
   height: 40px;
   margin-bottom: 10px;
   outline: medium none;
   padding-left: 10px;
   width: 100%;
}
.form-address{
   background: #F0F0E9;
   border: medium none;
   color: #696763;
   display: block;
   font-family: 'Roboto', sans-serif;
   font-size: 14px;
   font-weight: 300;
   margin-bottom: 10px;
   outline: medium none;
   padding-left: 10px;
   width: 100%;
}
.signup-btn{
   background:#FE980F;
   border: 0 none;
   border-radius: 0;
   color: #fff;
   font-family: 'Roboto', sans-serif;
   font-size: 15px;
   margin-bottom: 25px;
}





