
.checkout-button {
  background:#FE980F;
  border: 0 none;
  border-radius: 0;
  color: #FFFFFF;
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  margin-bottom: 50px;
  float: right;
  margin-top: 50px;
}
.checkout-button:hover {
  background: #e6e6e6;
  border: 0 none;
  border-radius: 0;
  color: #696763;
}
.modal-title{
  font-size: 20px;
  text-align: center;
}
.modal-text{
  font-size: 17px;
  text-align: center;
  margin-top: -20px;
}



